* {
  box-sizing: border-box;
  margin: 0;
}

.watch {
  z-index: 1;
  border: 2px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch:before {
  content: "";
  transform-origin: bottom;
  background-color: green;
  width: 1vmin;
  height: 39vmin;
  animation: 5s linear infinite move;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%);
}

.watch:after {
  content: "";
  transform-origin: 0;
  background-color: #00f;
  width: 19vmin;
  height: 2.5vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%);
}

.watch__center {
  z-index: 3;
  background-color: #f8e731;
  border-radius: 50%;
  width: 6vmin;
  height: 6vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

